<template>
  <div class="ele-upload-image">
    <!--  -->
    <el-upload
      :accept="accept"
      :action="action"
      :before-upload="handleBeforeUpload"
      :data="data"
      :disabled="uploading"
      :drag="Boolean(drag)"
      :headers="headers"
      :http-request="httpRequest"
      :limit="limit"
      :list-type="drag ? 'picture' : 'picture-card'"
      :multiple="multiple"
      :name="name"
      :on-change="handleChange"
      :on-error="handleUploadError"
      :on-exceed="handleExceed"
      :on-success="handleUploadSuccess"
      :show-file-list="false"
      :style="{
        marginBottom: multiple && computedValues.length ? '20px' : '0px'
      }"
      :withCredentials="withCredentials"
      ref="upload"
      v-if="!crop"
      v-show="isShowUpload"
    >
      <div v-loading="uploading">
        <!-- drag -->
        <template v-if="drag">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            
            <em></em>
          </div>
        </template>

        <!-- drag -->
        <template v-else>
          <div
            :style="{
              width: size + 'px',
              height: size + 'px',
              lineHeight: size + 'px'
            }"
          >
            <i class="el-icon-plus"></i>
          </div>
        </template>
      </div>

      <!--  -->
      <div class="el-upload__tip" slot="tip" v-if="showTip">
        
        <b style="color: #f56c6c">{{
          fileType.length ? fileType.join('/') : ''
        }}</b>
        
        <template v-if="fileSize">
          
          <b style="color: #f56c6c">{{ fileSize }}MB</b>
        </template>
      </div>
    </el-upload>

    <div v-if="crop">
      <div
        :style="{
          width: size + 'px',
          height: size + 'px',
          lineHeight: size + 'px'
        }"
        @click="isShowCrop = true"
        class="el-upload el-upload--picture-card"
        style="margin-bottom: 20px"
        v-show="isShowUpload"
      >
        <i class="el-icon-plus avatar-uploader-icon"></i>
      </div>

      <cropper
        :field="name"
        :headers="headers"
        :height="cropHeight"
        :noCircle="cropHeight !== cropWidth"
        :params="data"
        :url="action"
        :width="cropWidth"
        :withCredentials="withCredentials"
        @crop-success="handleCropSuccess"
        @crop-upload-fail="handleCropUploadError"
        @crop-upload-success="handleCropUploadSuccess"
        @src-file-set="handleSetFileSet"
        class="ele-upload-image--cropper"
        img-format="png"
        ref="cropper"
        v-if="isShowCrop"
        v-model="isShowCrop"
      ></cropper>
    </div>

    <!--  -->
    <ele-gallery
      :lazy="lazy"
      :remove-fn="handleRemove"
      :size="size"
      :sliceSingle="true"
      :source="value"
      :thumbSuffix="thumbSuffix"
      :title="title"
    />
  </div>
</template>

<script>
import Cropper from 'vue-image-crop-upload/upload-2.vue'
import EleGallery from 'vue-ele-gallery'

export default {
  name: 'EleUploadImage',
  props: {
    // 
    value: {
      type: [String, Array],
      default() {
        return []
      }
    },
    // 
    crop: {
      type: Boolean,
      default: false
    },
    // 
    size: {
      type: Number,
      default: 150
    },
    // 
    cropHeight: {
      type: Number
    },
    // 
    cropWidth: {
      type: Number
    },
    // (MB)
    fileSize: {
      type: Number
    },
    // 
    responseFn: Function,
    // , ['png', 'jpg', 'jpeg']
    fileType: {
      type: Array,
      default: () => []
    },
    // 
    isShowSuccessTip: {
      type: Boolean,
      default: true
    },
    // ,  (?imageView2/1/w/20/h/20)
    thumbSuffix: {
      type: String,
      default: ''
    },
    // 
    isShowTip: {
      type: Boolean,
      default: true
    },
    // 
    title: String,
    // 
    lazy: {
      type: Boolean,
      default: false
    },
    //  ()
    action: {
      type: String,
      required: true
    },
    // ()
    headers: Object,
    // ()
    limit: Number,
    //  ()
    drag: {
      type: Boolean,
      default: false
    },
    // 	 cookie  ()
    withCredentials: {
      type: Boolean,
      default: false
    },
    //  ()
    multiple: {
      type: Boolean,
      default: false
    },
    // ()
    data: Object,
    //  ()
    name: {
      type: String,
      default: 'file'
    },
    //  ()
    httpRequest: Function,
    // thumbnail-mode ()
    accept: String,
    // 
    beforeRemove: Function
  },
  components: {
    Cropper,
    EleGallery
  },
  data() {
    return {
      cropData: {},
      isShowCrop: false,
      uploading: false,
      fileList: []
    }
  },
  computed: {
    // 
    showTip() {
      return this.isShowTip && (this.fileType.length || this.fileSize)
    },
    computedValues() {
      if (this.value) {
        if (typeof this.value === 'string') {
          return [this.value]
        } else {
          return [...this.value]
        }
      } else {
        return []
      }
    },
    isShowUpload() {
      if (this.multiple) {
        return true
      } else {
        return this.computedValues.length === 0
      }
    },
    successFiles() {
      return this.fileList.filter((file) => file.status === 'success')
    }
  },
  watch: {
    isShowCrop(value) {
      if (value === false) {
        this.cropData = {}
      }
    }
  },
  methods: {
    handleSetFileSet(fileName, fileType, fileSize) {
      const uid = this.cropData.uid || new Date().getTime()
      this.cropData = {
        name: fileName,
        percentage: 0,
        size: fileSize,
        type: fileType,
        status: 'ready',
        uid: uid
      }
    },
    handleCropSuccess(b64Data) {
      this.cropData.url = b64Data
    },
    handleCropUploadError(status) {
      this.$message.error(', ')
      this.$emit('error', status)
    },
    handleCropUploadSuccess(response) {
      this.cropData.status = 'success'
      this.cropData.percentage = 100
      this.cropData.response = response
      const file = Object.assign({}, this.cropData)
      let index = this.fileList.findIndex((item) => item.uid === file.uid)
      if (index > -1) {
        this.fileList.splice(index, 1, file)
      } else {
        this.fileList.push(file)
      }
      this.handleUploadSuccess(response, file, this.fileList)
    },
    // 
    handleBeforeUpload(file) {
      let isImg = false
      if (this.fileType.length) {
        let fileExtension = ''
        if (file.name.lastIndexOf('.') > -1) {
          fileExtension = file.name.slice(file.name.lastIndexOf('.') + 1)
        }
        isImg = this.fileType.some((type) => {
          if (file.type.indexOf(type) > -1) return true
          if (fileExtension && fileExtension.indexOf(type) > -1) return true
          return false
        })
      } else {
        isImg = file.type.indexOf('image') > -1
      }

      if (!isImg) {
        this.$message.error(
          `, ${this.fileType.join('/')}!`
        )
        return false
      }

      if (this.fileSize) {
        const isLt = file.size / 1024 / 1024 < this.fileSize
        if (!isLt) {
          this.$message.error(` ${this.fileSize} MB!`)
          return false
        }
      }

      this.uploading = true
      return true
    },
    handleChange(file, fileList) {
      this.uploading = false
      this.fileList = fileList
    },
    // 
    handleExceed() {
      this.$message.error(`${this.limit}`)
    },
    // 
    handleUploadError(err) {
      this.uploading = false
      this.$message.error(', ')
      this.$emit('error', err)
    },
    // 
    handleUploadSuccess(response, file) {
      let url = response
      this.uploading = false
      if (this.isShowSuccessTip) {
        this.$message.success('')
      }
      if (this.responseFn) {
        url = this.responseFn(response, file, this.successFiles)
      }
      if (this.multiple) {
        if (Array.isArray(this.value)) {
          this.$emit('input', [...this.value, url])
        } else {
          this.$emit('input', [url])
        }
      } else {
        this.$emit('input', url)
      }
    },
    doRemove(index) {
      if (this.multiple) {
        const data = [...this.computedValues]
        data.splice(index, 1)
        this.$emit('input', data || [])
      } else {
        this.$emit('input', null)
      }
    },
    handleRemove(index) {
      if (!this.beforeRemove) {
        this.doRemove(index)
      } else if (typeof this.beforeRemove === 'function') {
        const file = this.multiple
          ? this.computedValues[index]
          : this.computedValues

        const before = this.beforeRemove(file, this.computedValues)
        if (before && before.then) {
          before.then(
            () => {
              this.doRemove(index)
            },
            () => {}
          )
        } else if (before !== false) {
          this.doRemove(index)
        }
      }
    }
  },
  mounted() {
    // body, 
    if (this.crop && this.$refs.cropper) {
      document.body.appendChild(this.$refs.cropper.$el)
    }
  }
}
</script>

<style>
.ele-upload-image {
  line-height: 1;
}
.ele-upload-image .el-loading-spinner {
  line-height: 1;
}
.ele-upload-image .el-icon-plus {
  vertical-align: middle;
}
.ele-upload-image .el-upload--picture-card {
  width: auto;
  height: auto;
  background: none;
  line-height: inherit;
}

/*  */
.vue-image-crop-upload.ele-upload-image--cropper {
  z-index: 99;
}
.ele-upload-image--cropper .vicp-drop-area {
  background-color: #fbfdff !important;
}
.ele-upload-image--cropper .vicp-icon1-arrow {
  border-bottom-color: #909399 !important;
}
.ele-upload-image--cropper .vicp-icon1-body {
  background-color: #909399 !important;
}
.ele-upload-image--cropper .vicp-icon1-bottom {
  border-color: #909399 !important;
}
</style>
